import React from 'react'
import PropTypes from 'prop-types'

const Nav = props => {
    
    const enlaces = [
        {
            "texto" : "inicio",
            "ruta": "./"
        },
        {
            "texto": "sobre mi",
            "ruta": "https://twitter.com/agu_stringa"
        },
        {
            "texto":"contacto",
            "ruta": "mailto:stringaagustin1@gmail.com"
        },
        {
            "texto": "redes sociales",
            "ruta": "https://www.instagram.com/agu.stringa/"
        }
    ];
    
    return (
        <>
            <nav>
                {
                    enlaces.map(({texto, ruta}) => {
                        return <a key={texto} href={ruta} target="_blank">{texto}</a>
                    })
                }
            </nav>
        </>
    )
}

Nav.propTypes = {

}

export default Nav
