import React from 'react'
import Card from './Card';

const Cards = () => {

 
    const data = [
        {
          "lang": "Python",
          "url": "https://cdn.svgporn.com/logos/python.svg",
          "fcolor": "#00c3ff",
          "scolor": "#ffff1c"
        },
        {
          "lang": "Java",
          "url": "https://cdn.svgporn.com/logos/java.svg",
          "fcolor": "#D31027",
          "scolor": "#EA384D"
        },
        {
          "lang": "React",
          "url": "https://cdn.svgporn.com/logos/react.svg",
          "fcolor": "#000428",
          "scolor": "#004e92"
        },
        {
          "lang": "Perl",
          "url": "https://cdn.svgporn.com/logos/perl.svg",
          "fcolor": "#cc2b5e",
          "scolor": "#753a88"
        },
        {
            "lang": "JavaScript",
            "url": "https://upload.wikimedia.org/wikipedia/commons/thumb/9/99/Unofficial_JavaScript_logo_2.svg/1200px-Unofficial_JavaScript_logo_2.svg.png",
            "fcolor": "#d6ba32",
            "scolor": "#ffd93d"
        },
        {
            "lang": "css",
            "url": "http://lineadecodigo.com/wp-content/uploads/2014/04/css.png",
            "fcolor": "#399dd8",
            "scolor": "#1a62b1"
        }
    ]
    return (
        <div className="wrap-cards">
        {
            data.map(({lang, url, fcolor, scolor}) => {
                return <Card key={lang} lang={lang} img={url} fcolor={fcolor} scolor={scolor}></Card>
            })
        }   
        </div>
    )
}
export default Cards
